
import React, { Component } from 'react'
import PropTypes  from 'prop-types'
import { connect } from 'react-redux'

const arr = ['', '', '', ' ', '']
const getCropsName = (cropsId, criosAndVarietiesList) => {
    for (let item of criosAndVarietiesList) {
        if (item.id === cropsId) {
            return item['name']
        }
    }
    return ''
}
const getVarietiesName = (varietiesId, criosAndVarietiesList) => {
    for (let item of criosAndVarietiesList) {
        for(let i of item.list) {
            if (i.id === varietiesId) {
                return i['name']
            }
        }
    }
    return ''
}
const getSoilType = (key, soilTypes) => {
    for (let item of soilTypes) {
        if (key === item.key) {
            return item['value']
        }
    }
    return ''
}
const ShowMessage = (props) => {
    const defaultValue  = props.fieldMessage.message
    console.error(props)
    const { criosAndVarietiesList, soilTypes} = props.message
    const feature = props.feature.feature
    return (
        <div className='next'>
            <div>
                <div>
                    <div>
                        <div>
                            <label></label>
                            <div>{defaultValue.plantingSeason}</div>
                        </div>
                    </div>
                    <div>
                        <div>
                            <label></label>
                            <div>{getCropsName(defaultValue.cropsId, criosAndVarietiesList)}</div>
                        </div>
                        <div>
                            <label></label>
                            <div>{getVarietiesName(defaultValue.varietiesId, criosAndVarietiesList)}</div>
                        </div>
                    </div>
                    <div>
                        <div>
                            <label></label>
                            <div>{getCropsName(defaultValue.prevCropsId, criosAndVarietiesList)}</div>
                        </div>
                        <div>
                            <label></label>
                            <div>{getVarietiesName(defaultValue.prevVarietiesId, criosAndVarietiesList)}</div>
                        </div>
                    </div>

                    <div>
                        <div>
                            <label>
                                
                            </label>
                            <div>{defaultValue.prevDensity}/</div>
                        </div>
                        <div>
                            <label>
                                
                            </label>
                            <div>{defaultValue.prevProduction}</div>
                        </div>
                    </div>
                    <div>
                        <div className='history'>
                            <label>/</label>
                            <div>:{defaultValue.maxProduction}, 
                                :{defaultValue.minProduction}, 
                                :{defaultValue.aveProduction || (0).toFixed(1)}</div>
                        </div>

                    </div>
                    <div>
                        <div>
                            <label></label>
                            <div>{[''][defaultValue.commonDisease]}</div>
                        </div>
                        <div>
                            <label></label>
                            <div>{[''][defaultValue.commonPests]}</div>
                        </div>
                    </div>
                </div>
                <div>
                    {/* <div>
                    <div className='soil'>
                        <label></label>
                        <div>
                            <input type="radio" name="soil" value='soil-y' ref={soily => this.soily = soily} /><span></span>
                            <input type="radio" name="soil" value='soil-n' ref={soiln => this.soiln = soiln} /><span></span>
                        </div>
                    </div>
                    <div>
                        <div>
                            <label></label>
                            <input type="file" name="" id="" />
                        </div>
                    </div>
                </div> */}


                    <div>
                        <div>
                            <label></label>
                            <div>{getSoilType(defaultValue.soilType, soilTypes)}</div>
                        </div>
                        {/* <div>
                        <label>1</label>
                        <input type="file" name="" id="" />
                    </div> */}
                    </div>


                    <div>
                        <div>
                            <label></label>
                            <div>{defaultValue.soilPh}</div>
                        </div>
                        <div>
                            <label></label>
                            <div>{defaultValue.organicMatter}</div>
                        </div>
                    </div>
                </div>
                <div className='second'>
                    <div>
                        <div className='weather'>
                            <label></label>
                            <div>
                                {['', ''][defaultValue.weatherStations]}
                            </div>
                        </div>
                        <div className='terrain'>
                            <label></label>
                            <div>
                                {['', ''][defaultValue.topography]}
                            </div>
                        </div>
                    </div>

                    <div>
                        <div>
                            <label></label>
                            <div>
                                {['', '', ''][defaultValue.soilPreparation]}
                            </div>
                        </div>
                        <div>
                            <label style={{width: '91px'}}></label>
                            <div>
                                {['', '', '', '', ''][defaultValue.commonNaturalDisasters]}
                            </div>
                        </div>
                    </div>
                    <div>
                        <div className='exact'>
                            <label></label>
                            <div>
                                {['', ''][defaultValue.drill]}
                            </div>
                        </div>

                    </div>
                    <div>
                        <div className='exact'>
                            <label></label>
                            <div>
                                {['', ''][defaultValue.fertilizer]}
                            </div>
                        </div>

                    </div>
                    <div>
                        <div className='exact'>
                            <label></label>
                            <div>
                                {['', ''][defaultValue.ypfj]}
                            </div>
                        </div>

                    </div>
                    <div>
                        <div className='exact'>
                            <label></label>
                            <div>
                                {['', ''][defaultValue.harvester]}
                            </div>
                        </div>

                    </div>
                    <div>
                        <div>
                            <label></label>
                            <div>{arr[defaultValue.irrigation]}</div>
                        </div>

                    </div>
                </div>
            </div>
            
        </div>
    )
        
}
ShowMessage.propTypes = {
    fieldMessage: PropTypes.object,
    feature: PropTypes.object,
    message: PropTypes.object
}
const mapStateToProps = (state) => {
    return {
        feature: state.feature,
        fieldMessage: state.fieldMessage,
        message: state.message
    }
}


export default connect(mapStateToProps)(ShowMessage)


